<template>
  <div>
      <div class="div" @click="startyin">开启录音</div>
        <br/>
      <div class="div" @click="stopyin">停止录音</div>
        <br/>
      <div class="div" @click="playyin">播放</div>
        <br/>
      <div class="div" @click="onBridgeReady">支付</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      localId: null
    };
  },
  mounted(){
    this.axio.get('/user/wx')
      .then(data=>{
        console.log('++++++++',data)
      })
  },
  methods: {
    startyin() {
      wx.startRecord();
    },
    stopyin() {
      wx.stopRecord({
        success: res => {
          this.localId = res.localId;
          alert(this.localId);
        }
      });
    },
    playyin() {
      wx.playVoice({
        localId: this.localId // 需要播放的音频的本地ID，由stopRecord接口获得
      });
    },
    onBridgeReady() {
      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: "wx2421b1c4370ec43b", //公众号名称，由商户传入
          timeStamp: "1395712654", //时间戳，自1970年以来的秒数
          nonceStr: "e61463f8efa94090b1f366cccfbbb444", //随机串
          package: "prepay_id=u802345jgfjsdfgsdg888",
          signType: "MD5", //微信签名方式：
          paySign: "70EA570631E4BB79628FBCA90534C63FF7FADD89" //微信签名
        },
        function(res) {
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            // 使用以上方式判断前端返回,微信团队郑重提示：
            //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
          }
        }
      );
    }
  }
};
</script>

<style scoped>
.div {
  height: 50px;
  width: 100px;
  border: 2px solid rebeccapurple;
}
</style>


